/* Global Styles */

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }
  
  body {
    font-family: Arial, sans-serif;
    line-height: 1.6;
    color: #333;
    background-color: #f9f9f9;
  }
  
  /* Translation Container */
  
  .translation-container {
    max-width: 800px;
    margin: 40px auto;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  /* Language Selector */
  
  .language-selector {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
  }
  
  .language-selector select {
    width: 150px;
    height: 30px;
    margin: 0 10px;
    padding: 5px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .language-selector span {
    font-size: 16px;
    margin: 0 10px;
  }
  
  /* Input Box */
  
  .input-box {
    margin-bottom: 20px;
  }
  
  .input-box textarea {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
    resize: vertical;
  }
  
  .input-box button {
    width: 100%;
    height: 40px;
    padding: 10px;
    font-size: 16px;
    border: none;
    border-radius: 5px;
    background-color: #4CAF50;
    color: #fff;
    cursor: pointer;
  }
  
  .input-box button:hover {
    background-color: #3e8e41;
  }
  
  .input-box button:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
  
  /* Output Box */
  
  .output-box {
    margin-top: 20px;
  }
  
  .output-box h3 {
    font-size: 18px;
    margin-bottom: 10px;
  }
  
  .output-box .typewriter {
    font-size: 16px;
    line-height: 1.5;
  }
  
  /* Responsive Design */
  
  @media (max-width: 768px) {
    .translation-container {
      margin: 20px auto;
    }
    .language-selector select {
      width: 120px;
    }
  }
  
  @media (max-width: 480px) {
    .translation-container {
      padding: 10px;
    }
    .language-selector {
      flex-direction: column;
    }
    .language-selector select {
      width: 100%;
      margin-bottom: 10px;
    }
    .input-box textarea {
      height: 100px;
    }
  }

  .social-links {
    position: absolute;
    top: 10px;
    right: 20px;
  }
  
  .social-links a {
    margin-left: 10px;
  }
  
  .social-links img {
    width: 30px;
    height: 30px;
  }